.logo {  
    position: relative;
    width: 610px;
    height: 400px;
}


.hexagon {
   fill: var(--gold);
    width: 503px;
   position: relative;
    top: -77px;
    left: 3px;
    z-index: 0;
}

.grease {
    fill:var(--slategrey);
    position: relative;
    top: -451px;
    width: 386px;
    left: -84px;
    z-index: 7;
}

.stripe {
    fill: var(--darkred);
    position: relative;
    top: -383px;
    width: 215px;
    left: 85px;
    z-index: 2;
}

.lightning {
    fill: var(--darkred);
    position: relative;
    top: 0px;
    width: 114px;
    left: 67px;
    z-index: 10;
}

.logo-text {
  font-size: 14px;
    fill:var(--slategrey);
    font-family: Cabin-Bold,Cabin;
    font-weight: 700;
    z-index: 7;
    position: relative;
    width: 168px;
    top: 113px;
    left: -39px;
}

  @media only screen and (max-width:600px) {

    .logo {
        width: 350px;
        height: 280px;
      }

    .hexagon {
        width: 350px;
        top: -51px;
        left: 2px;
    }

    .grease {
        top: -437px;
        width: 270px;
        left: 15px;
    }

    .stripe {
        top: -265px;
        width: 151px;
        left: -77px;
    }

    .lightning {
        top: 0px;
        width: 78px;
        left: 49px;
    }

    .logo-text {
        width: 118px;
        top: 85px;
        left: -25px;
    }





  }